#aboutMe {
    .about {
        .description {
            display: flex;
            flex-direction: column;

            p {
                margin-bottom: 15px;
            }

            .image {
                width: 60%;
                margin-bottom: 20px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

            }
        }

        .info {
            ul {
                display: flex;
                flex-direction: column;
                gap: 15px;
                margin-bottom: 40px;
                justify-content: center;

                li {
                    display: flex;
                    flex-direction: reverse;
                    align-items: center;

                    .title {
                        display: inline-block;
                        font-family: $headerFont, sans-serif;
                        font-size: 16px;
                        color: white;
                        position: relative;
                        margin-right: 15px;
                        border-bottom: 2px solid white;
                        letter-spacing: 1px;

                        &::after {
                            position: absolute;
                            top: 0;
                            right: -8px;
                            content: ':';
                        }
                    }

                    .value {
                        display: inline-block;
                        font-family: $secondaryFont, sans-serif;
                        font-size: 16px;
                        color: $color-light;
                        word-break: break-word;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}

@media (min-width: 767px) {
    #aboutMe {
        .about {
            .description {

                .image {
                    width: 40%;
                }
            }

        }
    }
}